/* 
GLOBAL STYLES
Site-wide styles.
*/

// Top-level page styles
html {
    scroll-behavior: smooth;
}

body {
  font-family: var(--font-family-body);
  font-size: var(--step-0);
  font-weight: var(--fw-light);
  line-height: 1.5;
  color: var(--clr-text-400);
  background: var(--clr-surface-700);
  overflow-x: hidden;

  &:is(.home) {
    .bg-image-fade {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      overflow: hidden;
      z-index: -2;

      > img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        transition: opacity 0.01s ease;
      }
    }
  }
}

// Spacing after main content between the footer
main {
  margin-block-end: var(--space-3xl);
}

// Custom scroll bar colour
* {
  scrollbar-color: var(--clr-brand-primary) var(--clr-surface-400);
  
  &::-webkit-scrollbar {
    width: 12px;
  }
  
  &::-webkit-scrollbar-track {
    background: var(--clr-surface-400);
  }
  
  &::-webkit-scrollbar-thumb {
    background: var(--clr-brand-primary);
    border-radius: 6px;
    border: 3px solid var(--clr-surface-400);
  }
}

// Global text selection
*::selection {
  background: var(--clr-brand-accent);
  color: var(--clr-surface-700);
  -webkit-text-stroke-width: 0;
}